@function em($size, $context: 16) {
  @return ($size/$context) * 1em; }

@function img-url($image) {
  @return url('../img/#{$image}'); }

@mixin image($image) {
  background-image: img-url($image); }

@mixin trans($what: all, $dur: 0.2s, $easing: ease) {
  transition: $what $dur $easing; }

@mixin smooth-scroll {
  -webkit-overflow-scrolling: touch; }

@mixin smooth-font {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

@mixin gpu {
  transform: translate3d(0,0,0); }

@mixin md {
  @include r(991) {
    @content; } }

@mixin sm {
  @include r(767) {
    @content; } }

@mixin xs {
  @include r(599) {
    @content; } }

@mixin f($name, $font-size: null, $line-height: null, $letter-spacing: null) {
  $font-family: null;
  @if $name == "reg" {
    $font-family: "America Regular"; }
  @if $name == "bold" {
    $font-family: "America Bold"; }
  font-family: $font-family, Helvetica Neue, Helvetica, Arial, sans-serif;
  @if $font-family != null {
    font-weight: normal; }
  @if $font-size != null {
    font-size: $font-size * 1px; }
  @if $line-height != null {
    line-height: $line-height/$font-size; }
  @if $letter-spacing != null {
    letter-spacing: ($letter-spacing / 1000) * 1em; } }

@mixin reg {
  font-family: "America Regular", Helvetica Neue, Helvetica, Arial, sans-serif; }
@mixin bold {
  font-family: "America Bold", Helvetica Neue, Helvetica, Arial, sans-serif; }
