.post {
	padding: 15px;
	margin: 0 auto;
	// max-width: 712px
	border-bottom: 2px solid rgba(5,45,73,0.06999999999999995);
	background: $white;
	&:not(:first-child) {
		margin-bottom: 15px; }
	&__frame {
		position: relative;
		padding-bottom: 56.25%;
		margin-bottom: 20px;
		width: 100%;
		height: 0;
		iframe {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			width: 100%;
			height: 100%; } }
	&__header {
		padding-bottom: 15px;
		@include clr; }
	&__info {
		float: left; }
	&__user {
		position: relative;
		display: block; }
	&__logo {
		margin-right: 12px;
		height: 48px;
		width: 48px;
		border-radius: 50%;
		overflow: hidden;
		background-position: 50% 50%;
		background-size: cover;
		vertical-align: top;
		display: inline-block; }
	&__links {
		padding-top: 5px;
		vertical-align: top;
		display: inline-block; }
	&__patrons {
		float: right; }
	&__img {
		margin: 0 -15px;
		padding-bottom: 40%;
		height: 1px; }
	&__patrons {
		margin-top: 14px;
		color: #A7B2B8;
		.icon {
			font-size: 10px; } }
	&__footer {
		margin-top: 30px; }
	&__comments,
	&__likes {
		@include bold;
		font-size: 11px;
		color: #4F687A;
		&:not(:first-child) {
			margin-left: 10px; } }
	.post__author-name {
		@include bold;
		color: $darkGray;
		font-size: 16px; }
	.post__date {
		font-size: 12px;
		color: #A7B2B8; }
	.post__title {
		margin-top: 15px;
		color: $darkGray;
		display: block; } }

.info {
	margin: 15px 0;
	@include bold;
	text-align: center;
	text-transform: uppercase; }
