.sidebar {
	margin-top: 20px;
	padding: 15px;
	background: $white;
	&__btn {
		display: inline-block;
		vertical-align: top;
		width: 100%;
		@include md {
			padding: 0 10px;
			width: 50%; }
		@include xs {
			width: 100%; }
		+ .sidebar__btn {
			margin-top: 15px;
			@include md {
				margin-top: 0; }
			@include xs {
				margin-top: 15px; } } }
	&__footer {
		padding-top: 20px;
		font-size: 0;
		@include md {
			margin: 0 -10px; } } }
