body {
	padding-top: 52px;
	color: $darkGray;
	background: #F3F4F5;
	@include f(reg, 14, 18); }

.logo {
	display: inline-block;
	vertical-align: top;
	color: $darkGray !important;
	@include f(bold);
	font-size: 24px;
	line-height: 50px;
	text-decoration: none !important;
	@include xs {
		font-size: 18px; } }

.section {
	&__title {
		margin-bottom: 30px; } }

.load-more-wrap {
	margin: 30px 0 15px; }
